.userWindowContainer {
  position: absolute;
  width: 240px;
  height: 130px;
  bottom: 100px;
  right: 20px;
  background-color: #fff;
  border-radius: 10px;
  border: none;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
}
.userImg {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
.userWindowName {
  position: absolute;
  left: -5px;
  bottom: 10px;
  color: #fff;
  font-size: 15px;
}
.activeUserImg {
  position: absolute;
  width: 30px;
  height: 30px;
  top: 10px;
  right: 10px;
  padding: 4px;
  background-color: rgb(61, 58, 58);
  border-radius: 50%;
}
