.containerDropdown {
  position: absolute;
  width: 23vw;
  height: 87vh;
  right: 0;
  top: 0;
  margin-top: 15px;
  margin-right: 15px;
  padding: 20px 15px;
  background-color: #fff;
  border-radius: 6px;
}
.headerDropdown {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
h3 {
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 5px;
  color: #696969;
}
.closeDropdown {
  width: 20px;
  height: 20px;
  margin-right: 3px;
  padding: 0px;
  background-color: white;
  border: none;
}
.closeDropdown:hover {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #a9a9a9;
}
.hoverExitDropdown {
  position: absolute;
  width: 45px;
  height: 20px;
  top: 45px;
  right: 5px;
  background-color: black;
  border-radius: 3px;
  font-size: 18px;
  color: #fff;
}
.hoverExitDropdownText {
  margin: 3px 0 0 7px;
}
.navButtonDropdown {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 40px;
}
.navButton {
  width: 20px;
  height: 20px;
  padding: 0px;
  background-color: #fff;
  border: none;
}
.navText {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 10px;
}
.nameButton {
  display: block;
  text-align: center;
  width: 33vw;
}
.nameButtonText {
  margin-left: 0;
}
